.marked {
  font-family: "ubuntu", "Tahoma", "Microsoft YaHei", arial,sans-serif;
  color: #444444; }

.marked img {
  max-width: 100%; }

.marked h1, .marked h2, .marked h3, .marked h4 {
  color: #111111;
  font-weight: 400;
  margin-top: 1em; }

.marked h1, .marked h2, .marked h3, .marked h4, .marked h5 {
  font-family: Georgia, Palatino, serif; }

.marked h1, .marked h2, .marked h3, .marked h4, .marked h5, .marked dl {
  margin-bottom: 16px;
  padding: 0; }

.marked p {
  margin: 0 0 25px; }

.marked h1 {
  font-size: 48px;
  line-height: 54px; }

.marked h2 {
  font-size: 36px;
  line-height: 42px; }

.marked h1, .marked h2 {
  border-bottom: 1px solid #EFEAEA;
  padding-bottom: 10px; }

.marked h3 {
  font-size: 24px;
  line-height: 30px; }

.marked h4 {
  font-size: 21px;
  line-height: 26px; }

.marked h5 {
  font-size: 18px;
  line-height: 23px; }

.marked a {
  color: #0099ff;
  margin: 0 2px;
  padding: 0;
  vertical-align: baseline;
  text-decoration: none; }

.marked a:hover {
  text-decoration: none;
  color: #ff6600; }

.marked a:visited {
  /*color: purple;*/ }

.marked ul, .marked ol {
  padding: 0;
  padding-left: 18px;
  margin: 0; }

.marked li {
  line-height: 1.6; }

.marked p, .marked ul, .marked ol {
  font-size: 16px;
  line-height: 1.6; }

.marked ol ol, .marked ul ol {
  list-style-type: lower-roman; }

.marked code, .marked pre {
  font-family: Consolas, Monaco, Andale Mono, monospace;
  background-color: #f7f7f7;
  color: inherit; }

.marked code {
  font-family: Consolas, Monaco, Andale Mono, monospace;
  margin: 0 2px; }

.marked pre {
  font-family: Consolas, Monaco, Andale Mono, monospace;
  line-height: 1.7em;
  overflow: auto;
  padding: 6px 10px;
  border-left: 5px solid #6CE26C; }

.marked pre > code {
  font-family: Consolas, Monaco, Andale Mono, monospace;
  border: 0;
  display: inline;
  max-width: none;
  max-width: initial;
  padding: 0;
  margin: 0;
  overflow: visible;
  overflow: initial;
  line-height: 1.6em;
  font-size: .95em;
  white-space: pre;
  background: 0 0; }

.marked code {
  color: #666555; }

.marked aside {
  display: block;
  float: right;
  width: 390px; }

.marked blockquote {
  border-left: .5em solid #eee;
  padding: 0 0 0 2em;
  margin-left: 0; }

.marked blockquote cite {
  font-size: 14px;
  line-height: 20px;
  color: #bfbfbf; }

.marked blockquote cite:before {
  content: '\2014 \00A0'; }

.marked blockquote p {
  color: #666; }

.marked hr {
  text-align: left;
  color: #999;
  height: 2px;
  padding: 0;
  margin: 16px 0;
  background-color: #e7e7e7;
  border: 0 none; }

.marked dl {
  padding: 0; }

.marked dl dt {
  padding: 10px 0;
  margin-top: 16px;
  font-size: 1em;
  font-style: italic;
  font-weight: bold; }

.marked dl dd {
  padding: 0 16px;
  margin-bottom: 16px; }

.marked dd {
  margin-left: 0; }

.marked table {
  *border-collapse: collapse;
  /* IE7 and lower */
  border-spacing: 0;
  width: 100%; }

.marked table {
  border: solid #ccc 1px; }

.marked table thead {
  background: #f7f7f7; }

.marked table thead tr:hover {
  background: #f7f7f7; }

.marked table tr:hover {
  background: #fbf8e9;
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out; }

.marked table td, .marked .table th {
  border-left: 1px solid #ccc;
  border-top: 1px solid #ccc;
  padding: 10px;
  text-align: left; }

.marked table th {
  border-top: none;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
  padding: 5px;
  border-left: 1px solid #ccc; }

.marked table td:first-child, .marked table th:first-child {
  border-left: none; }

